import React from 'react';

const Informacion = () => {
    return (
        <div class="col-lg-12 one">
            <header>
                <h1 className="titulo">Información</h1>
                <p className="linea">.</p>
            </header>
            <br />
            <h1>¿Qué es el bullying?</h1>
            <p>Es la exposición que sufre un niño a daños físicos y psicológicos de forma intencionada y reiterada por parte de otro, o de un grupo de ellos, cuando acude al colegio. El acosador aprovecha un desequilibrio de poder que existe entre él y su víctima para conseguir un beneficio (material o no), mientras que el acosado se siente indefenso y puede desarrollar una serie de problemas psicológicos que afecten directamente a su salud o incluso, en situaciones extremas, propiciar que quiera acabar con su vida mediante el suicidio.</p>
            <p>El bullying escolar se suele producir durante el recreo, en la fila para entrar a clase, en los baños, los pasillos, los cambios de clase, al entrar y salir del centro, en el transporte escolar o en el comedor. Aunque también puede ocurrir en el aula, cuando el profesor está escribiendo en la pizarra o mientras está atendiendo a otros alumnos.</p>
            {/* <a href="#menu-toggle" class="btn btn-default menu-toggle">Muestra menu</a> */}

            <br />
            <h2>¿Qué se considera bullying?</h2>
            <p>Según las Pautas padres y madres ante el acoso escolar, de Save the Children, las formas que utiliza el acosador para intimidar a su víctima se dividen de la siguiente manera:</p>
            <ul>
                <li>Acoso físico: El acosador golpea, empuja o utiliza algún instrumento para hacer daño físico a su víctima. También puede esconder sus cosas.</li>
                <li>Acoso verbal: Consiste en insultar, poner motes, hacer amenazas o provocar a otro niño.</li>
                <li>Acoso social: Este tipo de bullying se produce cuando el acosador decide aislar a su víctima difunde rumores, convence a otros niños para que no hablen con él o lo humillan en público para que el acosado se sienta aislado.</li>
                <li>Acoso sexual: Son todas las acciones que tienen que ver con los actos sexuales (como tocamientos no consentidos) o que se burlan de la orientación sexual de la víctima.</li>
                <li>Acoso por internet o cyber bullying: Es un tipo de acoso escolar que ocurre a través de móviles, tabletas, ordenadores, etc. En estos casos el acosador suele enviar mensajes de texto o correos electrónicos desagradables; difundir rumores a través del email o en las redes sociales, o imágenes y vídeos denigrantes, así como crear perfiles falsos que pueden resultar embarazosos.</li>
            </ul>

            <br/>
            <h2>¿Cómo buscar ayuda?</h2>
            <ul>
                <li class="puntos">Ayude a los niños a entender el acoso Explicarles qué es el acoso y cómo enfrentarlo de manera segura. Decirles a los niños que el acoso escolar es inaceptable. Asegurarse de que los niños sepan cómo recibir ayuda.</li>
                <li class="puntos">Mantener abiertas las líneas de comunicación. Hablar con los niños de forma regular. Escucharlos. Conocer a sus amigos, preguntarles sobre la escuela y comprender sus preocupaciones.</li>
                <li class="puntos">Aliente a los niños a hacer lo que aman. Las actividades, intereses y pasatiempos especiales pueden estimular la confianza, ayudar a los niños a hacer amigos y protegerlos de situaciones de acoso.</li>
                <li class="puntos">Dé el ejemplo de cómo tratar a otros con amabilidad y respeto.</li>
                <li class="puntos">Si el niño muestra cambios de humor o de comportamiento es importante tratar de hablar con ellos para determinar cuál es el motivo. Deben mantener la calma y escuchar todo lo que tenga que decir, siempre confiando en que lo que cuenta es la verdad. En caso de que el niño confiese sentirse acosado, hay que reforzar su autoestima y apoyarle, haciéndole saber que no es culpa suya.</li>
            </ul>
            <footer className="footer">
                <div class="footer-copyright text-center py-3">© 2020 Copyright:
            <a href="https://PrevenciónDelAcoso.com/"> #PrevenciónDelAcoso.com</a>
                    <br /><br />
                    <h5>Marcas que nos apoyan</h5>
                    <ul>
                        <li class="d-inline-flex p-2 bd-highlight"><img src="https://allied.build/images/google-reviews.png" width="50"
                            alt="google reviews" /></li>
                    </ul>
                    <h5>Contactanos</h5>
                    <ul>
                        <li class="d-inline-flex p-3 bd-highlight"><p>Por telefono: </p>
                            <a href="999-999-999"> 333-444-213</a> <br /></li>
                        <li class="d-inline-flex p-3 bd-highlight"></li>
                        <li class="d-inline-flex p-3 bd-highlight"><p>Por email: </p>
                            <a href="mailto:PrevenciónDelAcoso@website.com.mx"> PrevenciónDelAcoso@website.com.mx</a></li>
                    </ul>
                </div>
            </footer>
        </div>
    )
}

export default Informacion