import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  NavLink
} from "react-router-dom";
import Casos from './componets/Casos';
import Informacion from './componets/Informacion';
import Inicio from './componets/Inicio';
import Media from './componets/Media';

function App() {
  return (
    <Router>
      <div className="container">
        <div className="btn-group">
          <Link to="/" className="btn btn-dark" activeClassName="active">
          Inicio
          </Link>
          <Link to="/media" className="btn btn-dark" activeClassName="active">
          Media
          </Link>
          <Link to="/informacion" className="btn btn-dark" activeClassName="active">
          Información
          </Link>
          <Link to="/casos" className="btn btn-dark" activeClassName="active">
          Casos
          </Link>
        </div>
        <Switch>
        <Route path="/" exact>
            <Inicio/>
          </Route>
          <Route path="/media">
            <Media/>
          </Route>
          <Route path="/informacion">
            <Informacion/>
          </Route>
          <Route path="/casos">
            <Casos/>
          </Route>
        </Switch>
      </div>
    </Router>

  );
}


export default App;
